

body {
  margin: 0;
  padding: 0;
  font-family: poppins__subset, Helvetica, system-ui, -apple-system, BlinkMacSystemFont, segoe ui, Roboto, helvetica neue, Arial, noto sans, sans-serif, apple color emoji, segoe ui emoji, segoe ui symbol, noto color emoji;;
  font-size: 1.125rem;
  color: white;
  font-weight: 300;
  background-color: #0d1117;
  line-height: 1.3;
}

h1, h2, h3, h4, h5, h6{
  margin: 0;
}

ul{
  padding: 0;
}

.hero_text span {
  color: #61dafb;
}

.wrapper {
  margin-left: 1rem;
  margin-right: 1rem;
}


/* Utils */

.flex-wrap{
  display:flex;
  flex-wrap: wrap;
}


a {
  font-size: .9rem;
  text-decoration: none;
  color: white;
}

.m-0 {
  margin: 0;
}

.p {
  padding: .8rem;
}

.border-r {
  border-radius: 8px;
}

.container {
 padding: 1rem 0;
}

.ml {
  margin-left: .5rem;
}

.bg-light-black {
  background-color: #20232a;
}

.w-small {
  width: 1.56rem;
}


.f-p {
  font-size: 1.2rem;
}

.f-title {
  font-size: 2rem;
}

.flex {
  display: flex;
}

.flex-jc-sb {
  justify-content: space-between;
}

.flex-jc-c {
  justify-content: center;
}

.flex-ai-c {
  align-items: center;
}

.l-style {
  list-style-type: none;
}


/* End utils */

/* Header styles*/

header {
  height: 6rem;
}

nav {
  background-color: #161b22;
  padding-top: 0;
  padding-bottom: 0;
  position: fixed;
  z-index: 3;
  width: -webkit-fill-available;
  background: #161b22eb;
  backdrop-filter: saturate(180%) blur(5px);
  width: -webkit-fill-avaliable;
  height: 5rem;
  border-radius: 0 0px 9px 9px;
  padding: 0 1rem ;
}
nav ul li a:hover {
  color: #61dafb;
  -webkit-transition: color 0.2s ease-out;
  transition: color 0.2s ease-out;
}
.header__logo h3{
  color: #61dafb;
  font-size: 2rem;
}
/* End Header styles*/

/* Inputs and buttons*/
input {
  background-color: #0d1117;
  background-position: right center;
  padding: 0.05rem;
  border: solid 1px #30363d;
  border-radius: 6px;
  outline: none;
  padding: 5px 12px
}

input:focus{
  border-color: #418ba3;
  box-shadow: #0c2d6b;
}

button, .button {
  display: inline-block;
  padding: 8px 20px;
  background: linear-gradient(to right, #21262d, #0d1117);
  border: solid .01rem white;
  border-radius: 8px;
  font-weight: 400;
  color: white;
  cursor: pointer;
  font-size: 0.875rem;
  -webkit-transition: opacity 300ms ease-in-out;
  transition: opacity 300ms ease-in-out;
  margin-left: .5rem;
}

button:hover, .button:hover {
  opacity: 0.6;
}

/* End Inputs and buttons*/

/*  */
.line {
  background: #61dafb;
  width: fit-content;
  border-radius: 0 5rem 5rem 0;
  padding: .5rem;
  color: #20232a;
  font-size: 1.2rem;
}

.box{
  border: 1px solid white;
  border-radius: 6px;
  padding: 1rem;
  min-width: 255px;
  min-height: 182px;
  max-width: 500px;
}

.box-actions{
  justify-content: flex-end;
}

/*  */

/* Footer */

.footer {
  height: 6rem;
  display: flex;
  align-items: center;
}

footer h5{
  margin-bottom: 0;
  color: aliceblue;
}

.derechos{
  text-align: left;
  color: aliceblue;
  margin: 0;
  font-size: .8rem;
  font-weight: 100;
  margin-bottom: 1rem;
}

.footer-box{
  background: linear-gradient(180deg, #20232a, #20232af7);
  padding: 1rem;
  border-radius: 8px;
}

#email{
  color: #61dafb;
}

/* End Footer */

.not-found{
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100vh;
  flex-direction: column;
}

/* Small devices (landscape phones, 576px and up) */
@media (min-width: 576px) {

 }

/* Medium devices (tablets, 768px and up) */
@media (min-width: 768px) {
  .f-title {
      font-size: 3rem;
  }
  .f-p {
    font-size: 2rem;
  }
  .ml {
    margin-left: 2.5rem;
  }
  .line {
    font-size: 1.5rem;
  }

 }

/* Large devices (desktops, 992px and up) */
@media (min-width: 992px) {

  .f-title {
      font-size: 3.75rem;
  }

  a {
    font-size: 1rem;
  }

  .box{
    width: 500px;
  }

 }

/* X-Large devices (large desktops, 1200px and up) */
@media (min-width: 1200px) {
  .f-large {
    font-size: 3.75rem;
  }
  .wrapper{
    margin: 0 7rem;
  }
  nav{
    padding: 0 2rem;
    width: 76rem;
  }

  .flex-wrap {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }
 }
